export default {
  "@init"({ style }) {
    style.width = "100%";
  },
  ":root"({ data, output, style }, cate0, cate1, cate2) {
    cate0.title = "";
    cate0.items = [
      {
        title: "",
        type: "select",
        options: [
          {
            label: "",
            value: "top",
          },
          {
            label: "",
            value: "bottom",
          },
        ],
        value: {
          get({ data }) {
            return data.position;
          },
          set({ data }, value) {
            data.position = value;
          },
        },
      },
    ];
  },
};
