// import { styleEditor } from '../stickyItem/editors'

export default {
  '@init'({ data, style, slot }) {
    style.position = 'fixed';
    style.top = 600;
    style.right = 12;
    style.width = 40;
    style.height = 40;
  },
  "@resize": {
    options: ["width", "height"],
  },
  ':root': {
    style: [
      {
        title: '',
        options: ["background", "padding", "size"],
        target: 'mybricks-to-top',
      }
    ],
    items: [
      {
        title: '',
        type: 'imageSelector',
        value: {
          get({ data }) {
            return data.src;
          },
          set({ data }, value: any) {
            data.src = value;
          },
        }
      },
      {},
      {
        title: '',
        type: 'Radio',
        options: [
          { label: '', value: '0' },
          // { label: '', value: 'half' },
          { label: '', value: 'custom' },
        ],
        value: {
          get({ data }) {
            return data.visibleCondition;
          },
          set({ data }, value: any) {
            data.visibleCondition = value;
          },
        },
      },
      {
        title: '',
        type: 'Slider',
        ifVisible({ data }) {
          return data.visibleCondition === 'custom';
        },
        options: {
          max: 2000,
          min: 0,
        },
        value: {
          get({ data }) {
            return data.visibleHeight;
          },
          set({ data }, value: any) {
            data.visibleHeight = value;
          },
        },
      },
    ]
  }
};
