export default {
  "@init"({ style }) {
    style.width = "100%";
    style.height = "auto";
  },
  "@resize": {
    options: ["width", "height"],
  },
  ":root": {
    style: [
      {
        ifVisible({ data }) {
          return !data.asHotarea;
        },
        options: ["font", "padding", "border", "background", "boxShadow"],
        target: ".button",
      },
    ],
    items: [
      {
        //  ifVisible 
        ifVisible({ data }) {
          return !data.asHotarea;
        },
        title: "",
        type: "text",
        value: {
          get({ data }) {
            return data.text;
          },
          set({ data }, value: string) {
            data.text = value;
          },
        },
      },
      {
        title: "",
        description: " 50px",
        type: "switch",
        value: {
          get({ data }) {
            return data.asHotarea;
          },
          set({ data, style }, value: boolean) {
            //
            if (value) {
              if (style.height === "auto" || style.height === "100%") {
                style.flex = "unset";
                style.height = 50;
              }

              if (style.width === "auto" || style.width === "100%") {
                style.width = 50;
              }
            }

            data.asHotarea = value;
          },
        },
      },
      {}, //
      {
        title: "",
        type: "_Event",
        options: {
          outputId: "click",
        },
      },
    ],
  },
};
